<template>
  <div class="tag-wrapper">
    <yd-navbar title="All the Projects">
        <router-link to="/" slot="left">
            <yd-navbar-back-icon>Homepage</yd-navbar-back-icon>
        </router-link>
    </yd-navbar>

    <div class="tags"> 

        <yd-flexbox v-for="item, key in list" :key="key" class="item">
            <yd-flexbox-item>
                <div class="desc-wrapper">
                <div class="title"> 
                    <router-link to="/tag/1/"><yd-button  bgcolor="#efefef" class="tagname">{{item.tagname}}</yd-button>
                    </router-link>
                    <p> Comany name: {{item.companyname}}</p>
                    <span> Company score:</span> 
                <el-rate
                v-model="3.6"
                disabled
                show-score
                text-color="#ff9900"
                score-template="{value}">
                </el-rate>

                    <p>Project description: {{item.tag_desc}}</p>
                    <p>Requared Skills: <span class="skills">        <yd-badge shape="square" type="hollow" v-for="o,index in item.requaired_skills" :key="index" >{{o}}</yd-badge>
</span></p>
                    <span class="price"> Duration: {{item.duration}} months</span>
                </div>

            <div class="desc"> 
                <div class="action">
                    <router-link to="/tag/1/"><yd-button type="primary">Apply</yd-button></router-link>
                </div>
            </div>
            </div>
            </yd-flexbox-item>
             <!-- <div class="profile-wrapper">
                <img  :src="item.owner_profile">
                <yd-button type="hollow" class="tagname">{{item.username}}</yd-button>
            </div> -->
        </yd-flexbox>


        
    </div>
  </div>
</template>
<script>
export default {
  data(){
      return {
        list:[
            {
                tagname:'Web design job',
                companyname:'Google',
                tag_desc:'I need help for making my website. make sure looks awesome',
                requaired_skills:[
                    'Email','Print','finance','admin','calls'],
                price:'chicken',
                duration:'3',
                owner_profile:'https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg'
            },
            {
                tagname:'Maketing job',
                requaired_skills:[
                    'Email','Print','finance','admin','calls'],
                companyname:'Microsoft',
                tag_desc:'help me get more clients. I will tech you how to do it',
                duration:'2',
                price:'chicken',
                owner_profile:'https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg'
            },
            {
                tagname:'help for hire HR',
                companyname:'Hooli',
                duration:'4',
                requaired_skills:[
                    'Email','Print','finance','admin','calls'],
                tag_desc:'help me hire more interns',
                price:'chicken',
                owner_profile:'https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg'
            },
            {
                tagname:'makeing coffe Job',
                companyname:'Apple',
                duration:'6',
                requaired_skills:[
                    'Email','Print','finance','admin','calls'],
                tag_desc:'help our office making coffe',
                price:'chicken',
                owner_profile:'https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg'
            }
        ]
      }

  }
}
</script>
<style lang="stylus" scoped>
.tag-wrapper
    .tags
        .item
            background #ffffff
            border-bottom 1px solid #efefef
            padding 10px
        width 100%
        display inline-block
        .profile-wrapper

            display inline-block
            position flex
            max-width 100px
            img
                width 50%
                border-radius 50%

        .desc-wrapper
            .yd-badge-hollow
                margin-right 10px
            display inline-block
            position flex
            width 100%
            padding 10px
            .title
                text-align left 
            .desc
                text-align left
                padding 10px 0
                .action
                    margin-top 10px
</style>



